import React, { useEffect, useState } from 'react'
import MaterialTable from 'material-table'

const EconomySummaryTable = ({ accounts }) => {
  const [data, setData] = useState([])

  const mappedData = accounts.map((account) => ({
    accountTotal: account.totals.account,
    assessmentTotal: account.totals.assessments,
    bonusTotal: account.totals.bonuses,
    course: account.course,
    firstName: account.firstName,
    id: account.id,
    lastName: account.lastName,
    period: account.period,
    purchaseTotal: account.totals.purchases,
    songTotal: account.totals.songs,
  }))

  const [columns] = useState([
    { title: 'First Name', field: 'firstName' },
    { title: 'Last Name', field: 'lastName' },
    { title: 'Period', field: 'period' },
    { title: 'Account Total', field: 'accountTotal' },
    { title: 'Assessment Total', field: 'assessmentTotal' },
    { title: 'Bonus Total', field: 'bonusTotal' },
    { title: 'Purchase Total', field: 'purchaseTotal' },
    { title: 'Song Total', field: 'songTotal' },
  ])

  useEffect(() => {
    setData(mappedData)
  }, [accounts])

  return (
    <MaterialTable
      columns={columns}
      data={data}
      options={{ grouping: true, paging: false, toolbar: false }}
      style={{ boxShadow: 'none' }}
      title='Account Summary'
    />
  )
}

export default EconomySummaryTable
