import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import {
  AppBar,
  Button,
  Container,
  Dialog,
  Grid,
  IconButton,
  Paper,
  Slide,
  Toolbar,
  Typography,
  makeStyles,
} from '@material-ui/core'
import { Alert, AlertTitle } from '@material-ui/lab'
import { Close } from '@material-ui/icons'
import NumberFormat from 'react-number-format'

import EconomySummaryTable from './EconomySummaryTable'
import ListErrors from '../ListErrors'
import PageHeader from '../PageHeader'
import PageWrapper from '../PageWrapper'
import Spinner from '../Spinner'

import agent from '../../agent'

import {
  ECONOMY_DIALOG_LOADED,
  ECONOMY_DIALOG_UNLOADED,
} from '../../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  button: {
    marginRight: theme.spacing(2),
  },
  title: {
    color: theme.palette.common.white,
    margin: '0 auto',
  },
}))

const mapStateToProps = (state) => ({
  ...state.economy,
})

const mapDispatchToProps = (dispatch) => ({
  onLoad: () =>
    dispatch({
      type: ECONOMY_DIALOG_LOADED,
      payload: agent.Scores.getAll(),
    }),
  onUnload: () =>
    dispatch({
      type: ECONOMY_DIALOG_UNLOADED,
    }),
})

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction='up' ref={ref} {...props} />
})

const GridTile = ({ children, md, title, unit, value }) => (
  <Grid item xs={12} md={md || 4}>
    <Paper elevation={3} style={{ padding: '16px', overflow: 'scroll' }}>
      {title && (
        <Typography gutterBottom variant='h6'>
          {title}
        </Typography>
      )}
      {value && (
        <Typography align='center' variant='h5'>
          <NumberFormat
            displayType='text'
            suffix={value !== 1 ? ` ${unit}` : ` ${unit.slice(0, -1)}`}
            thousandSeparator={true}
            value={value}
          />
        </Typography>
      )}
      {children}
    </Paper>
  </Grid>
)

const Dashboard = ({ data }) => (
  <Grid container spacing={3}>
    <GridTile
      title='Total Points'
      unit='pts'
      value={data.reduce((prev, cur) => prev + (cur.totals.account || 0), 0)}
    />
    <GridTile title='Top Student' unit='pts' value={data[0]?.totals.account}>
      <Typography align='center' variant='body1'>
        {data[0]?.firstName} {data[0]?.lastName}
      </Typography>
    </GridTile>
    <GridTile
      title='Total Assessments'
      unit='pts'
      value={data.reduce(
        (prev, cur) => prev + (cur.totals.assessments || 0),
        0
      )}
    />
    <GridTile
      title='Total Bonuses'
      unit='pts'
      value={data.reduce((prev, cur) => prev + (cur.totals.bonuses || 0), 0)}
    />
    <GridTile
      title='Total Purchases'
      unit='pts'
      value={data.reduce((prev, cur) => prev + (cur.totals.purchases || 0), 0)}
    />
    <GridTile
      title='Total Songs'
      unit='songs'
      value={data.reduce((prev, cur) => prev + cur.totals.songs, 0)}
    />
    <GridTile title='Total Students' unit='students' value={data.length} />
    <GridTile md={12} title='Account Summary'>
      <EconomySummaryTable accounts={data} />
    </GridTile>
  </Grid>
)

const EconomyDialog = ({ errors, inProgress, onLoad, onUnload, students }) => {
  const [data, setData] = useState([])
  const [open, setOpen] = useState(false)

  const classes = useStyles()

  const handleClickOpen = () => {
    setOpen(true)
    onLoad()
  }

  const handleClose = () => {
    setOpen(false)
    onUnload()
  }

  useEffect(() => {
    setData(students.sort((a, b) => b.totals.account - a.totals.account))
  }, [students])

  return (
    <>
      <Button className={classes.button} onClick={handleClickOpen} size='small'>
        View Economy
      </Button>
      <Dialog
        fullScreen
        open={open}
        onClick={(ev) => ev.stopPropagation()}
        onClose={handleClose}
        onFocus={(ev) => ev.stopPropagation()}
        TransitionComponent={Transition}
      >
        <AppBar position='relative'>
          <Toolbar>
            <IconButton edge='start' onClick={handleClose}>
              <Close />
            </IconButton>
            <Typography variant='h5' className={classes.title}>
              Accounts
            </Typography>
          </Toolbar>
        </AppBar>
        <PageWrapper>
          <Container>
            <PageHeader title='Economy View' />
            <ListErrors errors={errors} />
            {inProgress ? (
              <Spinner message='Computing...' />
            ) : data.length > 0 ? (
              <Dashboard data={data} />
            ) : (
              <Alert severity='info'>
                <AlertTitle>No data available</AlertTitle>
                You must create at least one course, add students to that
                course, and add bonuses, purchases, or scores before any data
                will be visible here
              </Alert>
            )}
          </Container>
        </PageWrapper>
      </Dialog>
    </>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(EconomyDialog)
